body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100%;
  width: 100%;
}

.menu {
  background: white;
  font-size: 1.5em;
  display: flex;
  justify-content: space-around;
  height: 5vh;
  margin-top: 1vh;
  text-transform: uppercase;
  top:0;
  position: -webkit-sticky;
  position: sticky;
  font-family: 'Staatliches', cursive;
  z-index: 10;
  padding: .5%;
}


.current-menu {
  border-style: solid;
  border-color: #1C2F27;
  border-width: 0px 0px 0.5vh 0px;
}

.homepage, .overview-text, .diagram-layout {
  height: 100vh;
  align-content: center;
}

.home-title {
  font-size: 4em;
  text-transform: uppercase;
  text-align: center;
  font-family: 'Staatliches', cursive;
  padding: 2%;
  padding-top: 5%;
}

.home-divider {
  border: solid #1C2F27 0.5vh;
  width: 2vw;
  display: inline-block;
}

.home-subtitle {
  text-align: center;
  font-family: 'Staatliches', cursive;
  font-size: 2em;
  padding: 2em;
}

.home-quote {
  font-size: 3em;
  height: 20vh;
  padding-top: 10vh;
  font-family: 'Nixie One', cursive;
  text-align: center;
}

.centering-guide{
  width: 100%;
  text-align: center;
}

.slider-circle, .current-slider {
  background: transparent;
  display: inline-block;
  width: 1vh;
  height: 1vh;
  border-radius: 50%;
  border: solid black 2px;
  margin: 1%;
  margin-bottom: 2%;
}

.current-slider {
  background: black;
}

.down-arrow, .back-arrow, .front-arrow {
  display: inline-block;
  box-sizing: border-box;
  height: 3vh;
  width: 3vh;
  border-style: solid;
  border-color: black;
  border-width: 0px 1px 1px 0px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transition: border-width 150ms ease-in-out;
  transition: border-width 150ms ease-in-out;
}

.down-arrow:hover, .back-arrow:hover, .front-arrow:hover{
  border-bottom-width: 5px;
  border-right-width: 5px;
}

.overview-text {
  font-family: 'Nixie One', cursive;
  font-size: 1.2em;
  line-height: 1.5em;
  display: flex;
  flex-wrap: nowrap;
}

.overview-description {
  width: 50%;
  padding: 5%;
  background: #1C2F27;
  color:white;
}

.overview-icons {
  padding: 5%;
  padding-top: 0;
}

.icon-title {
  display: inline-block;
  font-size: 2em;
  padding-bottom: 5%;
}

.icon {
  width: 10vw;
  height: 10vw;
}

.icon-row {
  display: flex;
  flex-wrap: nowrap;
}

.icon-description {
  text-align: center;
  display: inline-block;
}

.diagram-slider-top{
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  height: 90vh;
}

.back-arrow {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

.front-arrow {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.diagram-content{
  height: 50vh;
}
